

.container{

    overflow: hidden;
    width :100%;
    padding: 3%;
    padding-top: 1%;
    box-sizing: border-box;
}


.itemBlock{
    padding :5px;
    box-sizing: border-box;
    position: relative;
}
.items{
    border: 0.1px solid grey;
    background-color: white;
    height:100%;
    width:100%;
    display: grid;
    align-items: center;
    justify-items: center;
    cursor: pointer;
    grid-template-columns: 60% 40%;
    transition: 0.33s all;
    position: relative;
    overflow: hidden;
    
}
.items:hover{
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.viewMoreContainer{
    position: absolute;
    /* background-color: azure; */
    right: 0;
    bottom: 0;
    padding: 2%;
}

.itemDescriptionContainer{
    display: flex;
    align-items: center;
    flex-direction:column;
    justify-content:center;
    /* width:100%;
    height: 100%; */
    /* padding-top: 25%;
    box-sizing: border-box; */
}
.itemDescription{
    width:100%;
    font-size: 20px;
    font-weight: 300;
}
.itemName{
    /* position: absolute; */
    color: black;
    font-size: 30px;
    width:100%;
    /* background-color: red; */
    /* text-align: center; */
    font-weight: bold;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
}



.ItemoverlayContainer{
    width:100%;
    height:100%;
    z-index: 500;
    top :0;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    /* From https://css.glass */
    /* From https://css.glass */
    background: rgba(7, 7, 7, 0.2);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(7.9px);
    -webkit-backdrop-filter: blur(7.9px);
}
.itemOverlay{
    /* border: solid 0.5px black; */
    background-color: white;
    width:90%;
    height:80%;
    color: black;
    padding: 2%;
    box-sizing: border-box;
    border-radius: 2em;
    overflow: hidden;


    display :grid;
    grid-template-columns: 50% 50%;

}



