




.container{
    width:100%;
    /* background:blue; */
    display: grid;
    padding:5px;
    box-sizing: border-box;
    
    
}

.items{
    transition: all 0.5s;
    overflow: hidden;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box;
    text-align: center;
    -webkit-line-clamp: 1;
    /* background-color: red; */
    -webkit-box-orient: vertical;
    word-wrap: break-word;
    color: grey;
    transition: all 0.5s;
    cursor: pointer;
    padding: 2%;
    box-sizing: border-box;
}
.items:hover{
    opacity: 60%;
}